@font-face {
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-family: "Jaapokkisubtract";
  src: url("../app/fonts/jaapokkisubtract.woff2");
}
@font-face {
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue";
  src: url("../app/fonts/helveticaneue.woff2");
}
@font-face {
  font-weight: 300;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Light";
  src: url("../app/fonts/helveticaneue-light.woff2");
}
@font-face {
  font-weight: 500;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Medium";
  src: url("../app/fonts/helveticaneue-medium.woff2");
}
@font-face {
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Bold";
  src: url("../app/fonts/helveticaneue-bold.woff2");
}
:root {
  --font-body: "HelveticaNeue", sans-serif;
  --font-title: "Jaapokkisubtract", sans-serif;
  --transition: .2s;
}

:root {
  --special: hsl(229, 86%, 58%);
  --text: hsl(0, 0%, 10%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  letter-spacing: 1.6px;
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-title);
}

a,
a:visited,
a:active {
  color: var(--white);
  text-decoration: none;
  transition: var(--transition) ease-in-out;
}

.container {
  padding: 0 1.25rem;
  max-width: 83.75rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    transform: translate(0, 0);
    padding: 0;
  }
}

.header {
  background-image: linear-gradient(var(--special) 50%, var(--text) 50%);
  color: var(--white);
  min-height: 100vh;
  font-weight: 600;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.375rem 0;
}
.header__menu *:not(:last-child) {
  margin-right: 1.875rem;
  float: left;
}
.header__logo {
  transform: translate(-50%);
}
.header__promo {
  display: flex;
  align-items: center;
  min-height: 43.75rem;
  background-image: url("../images/header.webp");
}
.header__buttons {
  position: absolute;
  top: 5.625rem;
  left: 6.875rem;
}
.header__text {
  margin-left: 6.25rem;
  font-size: 1.875rem;
}
.header__text h1 {
  font-size: 5.3125rem;
}